import React, {FC, Suspense, useEffect} from "react"
import {
    createTheme,
    ThemeProvider, useMediaQuery
} from "@mui/material";
import Loader from "../common/Loader";
import {themes} from "../../theme";
import {appColorModeAtom} from "../../store";
import {useAtom} from "jotai";

const Layout = React.lazy(() => import("../layout/Layout"))

interface ICProps {
}


const App: FC<ICProps> = () => {
    const prefersDarkMode = useMediaQuery("(prefers-color-scheme: dark)");
    const [appColorMode, setAppColorModeAtom] = useAtom(appColorModeAtom)

    const setStoredColorMode = () => {
        const colorMode = localStorage.getItem('colorMode')
        if (colorMode) {
            setAppColorModeAtom(JSON.parse(colorMode))
        }
        return !!colorMode
    }

    useEffect(() => {
        const isSet = setStoredColorMode()
        if (!isSet) {
            const mode = prefersDarkMode ? 'dark' : 'light'
            setAppColorModeAtom(mode)
            localStorage.setItem('colorMode', JSON.stringify(mode))
        }
    }, [prefersDarkMode])

    const theme = createTheme(themes[appColorMode])

    return (
        <ThemeProvider theme={theme}>
            <Suspense fallback={<Loader/>}>
                <Layout/>
            </Suspense>
        </ThemeProvider>
    )
}

export default App
